body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-family: 'B612', sans-serif;
}
.topbar{padding:1% 5%}.topbar .navbar-brand{float:center}.topbar img{height:75px}.navbar{margin-top:-10px}.navbar-toggler-icon{color:rgba(0,0,0,0.5);background-image:url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%280, 0, 0, 0.5%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e")}.nav-item-container li{font-family:"B612 Bold", sans-serif;color:#919191;padding:0 10px;font-size:125%;font-weight:800}.nav-item-container li a:hover{color:#06f !important;cursor:pointer;transition-duration:0.3s}

.footer{width:100%;background-color:#232323;padding-bottom:15px}.footer .format{margin-top:32.5px;margin-left:-15px;margin-right:5px;background-color:#06f;width:7.5px;height:40px;float:left;border-radius:0 15px 0 15px}.footer .col-md-3{padding:0;margin:0 35px}.footer .container{margin-left:5%}.footer p{font-family:"B612 Bold", sans-serif;padding-top:30px;font-size:200%;font-weight:900;margin-bottom:0px;color:#fff}.footer ul li a{font-size:125%;padding:5px 0;width:45%;line-height:150%;color:#919191;font-weight:400}.footer ul li a:hover{color:#fff;font-weight:700;text-shadow:#000;cursor:pointer;transition-duration:0.3s}

.mid{margin-bottom:50px}.mid h2{text-align:center;color:#919191;font-size:225%;font-family:"B612 Bold", sans-serif;font-weight:600;margin-bottom:-10px}.mid h1{text-align:center;color:#06f;font-family:"B612 Bold", sans-serif;font-size:275%;font-weight:900;margin-bottom:25px}.mid .card-container .col{margin:15px 0}.mid .card-container .col .card{border-radius:1rem !important;padding-bottom:10px;box-shadow:0 15px 25px rgba(0,0,0,0.2);border:none}.mid .card-container .col .card .card-body{padding-left:10%;padding-top:5%}.mid .card-container .col .card .format{margin-top:0px;margin-left:-15px;margin-right:5px;background-color:#06f;width:7.5px;height:50px;float:left;border-radius:0 15px 0 15px}.mid .card-container .col .card .card-title{font-family:"B612 Bold", sans-serif;font-size:175%;font-weight:900;margin-right:-5%;color:#06f;margin-bottom:10px;line-height:100%}.mid .card-container .col .card .card-subtitle{font-family:"B612", sans-serif;font-size:125%;font-weight:800;margin-bottom:0px;color:#919191;margin-bottom:10px;line-height:100%}.mid .card-container .col .card .card-img-top{padding:5% 0}.mid .card-container .col .card .card-text{font-family:"B612 Bold", sans-serif;font-size:115%;font-weight:700;color:#C1C1C1}

.breaker{background:#06f;height:360px}

